import Fetch from 'isomorphic-unfetch';

class WeatherMap extends React.Component{

  state = {
    mapImage: undefined,
    err: undefined
  }

  componentDidMount(){
    this.getWeatherData();
    console.log(this.state);
  }

  getWeatherData = async() => {
    try{
      const req = await fetch('https://tile.openweathermap.org/map/clouds_new/1/1/1.png?appid=038b0cc9c6214552f03247312f5f166a')
      const response = await req;

      this.setState({
        mapImage:response
      });
    } catch(e){
      this.setState({err: e.message});
    }
  }

  render(){
    return(
      <div>
        <img src={this.mapImage} alt="Logo" />
      </div>
    );
  }
}

export default WeatherMap;
//https://tile.openweathermap.org/map/{layer}/{z}/{x}/{y}.png?appid={api_key}
